import css from'./Player.module.scss';
import React from 'react';
import { TweenLite } from "gsap";
class Player extends React.Component
{
    constructor(props)
    {
        super(props);
        this.colors=["blue","red","pink","yellow"];
        this.state={
            cx2: props.cx,
            cy2: props.cy,
            level:props.level,
            r: props.r,
            color: props.color,
            pos:props.pos,
            max:props.max,
            y:0,
            angle:0,
            loaded:0
        }
        this.myTween = null;
    }
    componentDidMount()
    {
        this.setState({y:this.state.cy2 - 275 + 2.3 * this.state.r * this.state.level,angle:this.state.pos/this.state.max*360, loaded:1})

    }
    static getDerivedStateFromProps(props, state) {
        if(state.loaded===1)
        {
            console.log("A")
        }
        return {
            ...props.f,
          };
    }
    render()
    {
        //let transform=""
        let transform=`rotate(${this.state.angle},${this.state.cx2},${this.state.cy2})`
        return(<circle ref={this.cir} cx={this.state.cx2} cy={this.state.y} r={this.state.r} fill={this.state.color} className={css.playerCircle} transform={transform}></circle>);
    }
}
export default Player;