import React from 'react';
import Property from './Property';
import Player from './Player';
import css from './Circle.module.scss'
class Circle extends React.Component
{
    constructor(props)
    {
        super(props);
        this.colors=["blue","red","pink","yellow"];
        this.test=0;
        this.state={
            background: 'gray',
            cx2: 1000,
            cy2: 410,
            test:0,
            circle: {},
            number: 30,
            pnum:4,
            data: [{type: "property",level:5},{ownerColor: "pink"},{groupColor: "red"}],
            pPos:[0,2,2,0],
   
        }
        this.interval = this.interval.bind(this);
        this.intervalTime=1000;
        
        
    }
    ws=new WebSocket('ws://localhost:8000/');
    drawCircle(r) {
        var el={};
        el.r=r;
        el.stroke="black";
        return el;
    }
    componentDidMount()
    {
        document.body.style.backgroundColor = this.state.background;
        var circle=this.drawCircle(300);
        this.setState({circle});
        var interval=setInterval(this.interval,this.intervalTime);
        this.setState({interval});
        //document.getElementById("canvas").innerHTML = document.getElementById("canvas").innerHTML;
        this.ws.onopen = () => {
            // on connecting, do nothing but log it to the console
            console.log('connected')
            }
        this.ws.onmessage = evt => {
            // listen to data sent from the websocket server
            //const message = JSON.parse(evt.data)
            //this.setState({dataFromServer: message})
            console.log(evt.data)
            }
        
        this.ws.onclose = () => {
        console.log('disconnected')
        // automatically try to reconnect on connection loss
        }
        this.ws.onerror = err => {
            console.error(
                "Socket encountered error: ",
                err.message,
                "Closing socket"
            )
        }
  
    }

    connect()
    {
        

    }


    interval()
    {
       /*
            if(this.test)
            {
            this.setState({data: [{ownerColor:"red"}]});
            this.test=0;
            }
            else
            {
            this.setState({data: [{ownerColor:"blue"}]});
            this.test=1;
            }
            */
            
        
    }
    render()
    {
const elems=[];
const players=[];
for(let i=0;i<this.state.number;i++)
{
elems.push(<Property key={"Prop"+i} max={this.state.number} num={i} data={this.state.data[i]}></Property>)
}
var r = 15;
for (var i = 0; i < 4; i++) {
    let level=0
    let pos=this.state.pPos[i]
    for(var j=0;j<i;j++)
    {
        if(pos===this.state.pPos[j])level++
    }
    
    players.push(<Player cx={this.state.cx2} cy={this.state.cy2} color={this.colors[i]} r={r}  key={"Cir"+i} level={level} pos={pos} max={this.state.number}></Player>)
}

return (
<div>
<svg className={css.svgArrow} id="canvas" width="1500" height="820">
<rect id="background" className={css.background} />
{elems}
<circle fill={this.state.background} cx={this.state.cx2} cy={this.state.cy2} r={this.state.circle.r} className={css.fillInCircle}></circle>
{players}
</svg>
<div>transform="rotate(100,1000,410)"</div>
</div>
)
}
}
export default Circle;